import React from 'react';
import './App.css';

/**** UTILITIES ****/
import Aside from './utilities/Aside.js';
import Header from './utilities/Header.js';
import Content from './utilities/Content.js';
import ScrollPanel, {ScrollPanelItem} from './utilities/ScrollPanel.js';

/**** SUBCONTENT ****/
import ANConcept from './portfolio/ANConcept';
import ANProto from './portfolio/ANProto.js';
import Camera from './portfolio/Camera.js';
import Cloth from './portfolio/Cloth.js';
import Color from './portfolio/Color.js';
import DJPong from './portfolio/DJPong';
import Freqs from './portfolio/Freqs.js';
import FunLoop from './portfolio/FunLoop';
import Lightfields from './portfolio/Lightfields.js';
import Meshedit from './portfolio/Meshedit.js';
import Morph from './portfolio/Morph.js';
import Physics from './portfolio/Physics.js';
import PT1 from './portfolio/PT1.js';
import PT2 from './portfolio/PT2.js';
import Rasterizer from './portfolio/Rasterizer.js';
import Visuality from './portfolio/Visuality.js';
import PythonFacts from './other/PythonFacts.js';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.resume = <object data={require("./other/resume.pdf")} type="application/pdf" width="100%" height="99.9%"><p>Resume</p></object>;
    this.contents = {


      "Home": <Content content={<div>
        <p>Welcome, welcome! You'll find computer science or video game-related stuff here.</p>
        <h3>Random Python 3 Fact (experimental)</h3>
          <PythonFacts/>
      </div>}/>,

      "CS 61A": <Content content={<div>
        <p><a href="https://links.cs61a.org/gibbes">Fill out attendance</a> weekly when present in discussion.</p>
        <h3>SECTIONS</h3>
        <ul>
          <li><strong>Lab</strong>: Tu 9:30 am - 10:59 am Soda 273</li>
          <li><strong>Discussion</strong>: Th 9:30 am - 10:59 am Hearst Mining 310</li>
          <li><strong>Office Hours</strong>: Th 7:30 pm - 8:00 pm Cory 241</li>
        </ul>
        <h3>COFFEE SESSIONS</h3>
        <p><a href="https://calendar.google.com/calendar/selfsched?sstoken=UUlUOEtEMGRZV01VfGRlZmF1bHR8NmY4MGJmOGE0YWMzNDE2YWZlNTBkZmE1Y2I3MGNlNjI">APPOINTMENTS HERE</a></p>
        <h3>SLIDES</h3>
        <ul>
					<li><a href="https://docs.google.com/presentation/d/1EpJa4c1yc1RyKXm0acjUsLY0XpO9gAnzWkoFsD0Zrsk/edit?usp=sharing">Intro to 61A</a></li>
          <li><a href="https://docs.google.com/presentation/d/1vPEpbW-N11gNl3Qdz2t3Z9KGyLZTmYJoe3dJBFwZGM0/edit?usp=sharing">Environment Diagrams</a></li>
          <li><a href="https://docs.google.com/presentation/d/1ydsfzeE6ioiusm6yaj9JvhBlV0udoOU1Ue6WaMkoEew/edit?usp=sharing">HOF and Recursion</a></li>
          <li><a href="https://docs.google.com/presentation/d/1CvFcTQi-kuze_GcMzhih7iIoSNKc0S30A2TJpv_5QGc/edit?usp=sharing">Tree Recursion</a></li>
          <li><a href="https://docs.google.com/presentation/d/1nh1hJY88nEqLGBBgwLBjWsyDR_Gvg7FxAYtZSahfJwA/edit?usp=sharing">Sequences and Trees</a></li>
          <li><a href="https://docs.google.com/presentation/d/109haXVSqlUmyqAb1qMVYFHSEQiHs-twzyW3aylgl5oo/edit?usp=sharing">Side Effects</a></li>
          <li><a href="https://docs.google.com/presentation/d/1W1lbzashtkv-A8p5sVPiVzVG2mSuXOzp7-M9hWfzXp4/edit?usp=sharing">Objects</a></li>
				</ul>
        <h3>MOCKS</h3>
        <ul>
					<li><a href="https://drive.google.com/file/d/0B9Dav46-FWzzUFlHRkFEU1ZTLXM/view?usp=sharing">Mock Midterm 1</a> and <a href="https://drive.google.com/file/d/0B9Dav46-FWzzU09oSENrbFdGMTg/view?usp=sharing">Solutions</a></li>
					<li><a href="https://drive.google.com/open?id=0B9Dav46-FWzzMUtjWXpxc01CRVE">Mock Midterm 2</a> and <a href="https://drive.google.com/open?id=0B9Dav46-FWzzTmFxN3BfSWF6UWc">Solutions</a></li>
					<li><a href="https://cs61a.org/assets/pdfs/mock-exams/61a-sp17-mock-final.pdf">Mock Final</a> and <a href="https://cs61a.org/assets/pdfs/mock-exams/61a-sp17-mock-final-sol.pdf">Solutions</a></li>
				</ul>
      </div>}/>,


      "Portfolio": <Content content={<div>
        <h3>CS 194-26 - Computational Photography</h3>
        <ScrollPanel items={[
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/jelly.png")}  alt="lightfield"   key="Lightfields"   title="Lightfields"   date="October 30 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/face.png")}  alt="faces"   key="Morphing Faces"   title="Morphing Faces"   date="October 18 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/mumu.jpg")}  alt="milkshakes"   key="Freqs & Gradients"   title="Freqs & Gradients"   date="October 1 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/library.JPG")}  alt="library"   key="Camera Obscura"   title="Camera Obscura"   date="September 14 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/nativity.jpg")}  alt="nativity"   key="Color Photos"   title="Color Photos"   date="September 5 2018"/>,
        ]}/>
        <h3>CS 184 - Graphics</h3>
        <ScrollPanel items={[
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/henge.png")}  alt="stonehenge"   key="Physics Smash"   title="Physics Smash"   date="May 1 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/cloth.png")}  alt="clothsim"     key="Cloth Simulator" title="Cloth Simulator" date="April 9 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/derg.png")}   alt="pathtracer 2" key="Pathtracer P2"   title="Pathtracer P2"   date="March 19 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/cow.png")}    alt="pathtracer 1" key="Pathtracer P1"   title="Pathtracer P1"   date="March 5 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/teapot.png")} alt="meshedit"     key="Meshedit"        title="Meshedit"        date="February 19 2018"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/map.png")}    alt="rasterizer"   key="Rasterizer"      title="Rasterizer"      date="February 5 2018"/>
        ]}/>
        <h3>ART 178 - Video Game Dev</h3>
        <ScrollPanel items={[
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/an.png")}         alt="AN demo"     key="'AN' Prototype"   title="'AN' Prototype"   date="November 29 2017"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/an_concept.png")} alt="concept"  key="'AN' Concept"     title="'AN' Concept"     date="October 25 2017"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/fun.jpg")}        alt="fun loop"    key="Fun Loop"         title="Fun Loop"         date="September 20 2017"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/eye.png")}        alt="visuality"   key="On Visuality"     title="On Visuality"     date="September 11 2017"/>,
          <ScrollPanelItem handlePageChange={this.handlePageChange} src={require("./img/pong.png")}       alt="dj pong"     key="DJ Pong"          title="DJ Pong"          date="August 30 2017"/>
        ]}/>
      </div>}/>,


      "Resume": <Content content={<object width="100%" height="100%" type="application/pdf" data={require("./other/resume.pdf")}><a href="http://gibby.me/resume.pdf">Resume</a></object>}/>,


      "About": <Content content={<div>
        <p>I am a UC Berkeley fourth-year undergraduate CS major and aspiring game developer.</p>
        <p>This website is made using classic HTML/CSS/Javascript along with the <a href="https://reactjs.org/">REACT</a> framework.</p>
        <img src={require("./img/prof.jpg")} alt="Gibbes" height="200px" padding="20px" className="prof"/>
        <h3>Contact Info</h3>
        <ul>
					<li><a href="https://github.com/gibbes">github.com/gibbes</a></li>
					<li><a href="https://linkedin.com/in/michael-gibbes/">linkedin.com/in/michael-gibbes</a></li>
					<li><a href="mailto:mgibbes@berkeley.edu">mgibbes@berkeley.edu</a> - I love getting e-mails!!!</li>
				</ul>
      </div>}/>,

      "Color Photos": <Content content={<Color/>}/>,
      "Camera Obscura"  : <Content content={<Camera/>}/>,
      "Freqs & Gradients" : <Content content={<Freqs/>}/>,
      "Morphing Faces" : <Content content={<Morph/>}/>,
      "Lightfields" : <Content content={<Lightfields/>}/>,
      "Physics Smash": <Content content={<Physics/>}/>,
      "Cloth Simulator": <Content content={<Cloth/>}/>,
      "Pathtracer P2": <Content content={<PT2/>}/>,
      "Pathtracer P1": <Content content={<PT1/>}/>,
      "Meshedit": <Content content={<Meshedit/>}/>,
      "Rasterizer": <Content content={<Rasterizer/>}/>,
      "'AN' Prototype": <Content content={<ANProto/>}/>,
      "'AN' Concept": <Content content={<ANConcept/>}/>,
      "Fun Loop": <Content content={<FunLoop/>}/>,
      "On Visuality" : <Content content={<Visuality/>}/>,
      "DJ Pong"  : <Content content={<DJPong/>}/>,
    };
    this.state = {header: "Home"};
  }

  handlePageChange(header) {
    this.setState({header});
  }

  render() {
    return (
      <div className = "Container">
        <Aside onPageChange={this.handlePageChange}/>
        <Header header={this.state.header}/>
        {this.contents[this.state.header]}
      </div>
    );
  }
}



