import React from 'react';

export default class Visuality extends React.Component {
    render() {
        return (
        <div>
            <h3>Presentation about Visual Culture</h3>
            <figure height="100%" margin="0">
                <iframe title="Presentation" src="https://docs.google.com/presentation/d/1ki5V4k3KFbQiP-aI_p2CgQzYTD5wsmE_RpcPNuIdQ9k/embed?start=false&amp;loop=false&amp;delayms=3000" allowFullScreen="true" height="450px" width="75%"/>
            </figure>
        </div>
        );
    }
}



