import React from 'react';

export default class Camera extends React.Component {
    render() {
        return (
        <div>
            <h1 align="center">Project 2: Building a Pinhole Camera</h1>
            <h2 align="center">By Jacob Green, Michael Gibbes, and Mrinalini Sugosh</h2>

            <p>In constructing our camera obscura, we used:</p>

            <ol>
                <li>A cardboard box of approximately <code>30x15x15 cm</code> in <code>lxwxh</code>.</li>
                <li>Black construction paper for all sides except the screen, which was white computer paper.</li>
                <li>Black construction paper for the pinholes, which were created by poking with a literal pin.</li>
                <li>Red duct tape.</li>
                <li>A Canon camera.</li>
            </ol>

            <p>In retrospect, we should not have used the red duct tape because the first model flooded the picture with red light. To mitigate this, we covered the box with an extra layer of black construction paper.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image13.JPG")} width="480px" alt="camera"/>
                        <figcaption align="middle">First Model</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image14.JPG")} width="480px" alt="camera"/>
                        <figcaption align="middle">Mitigated Red</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>Our 3 pinhole sizes included holes of size <code>0.5mm</code>, <code>1.0mm</code>, and <code>4.0mm</code>. During the experiment, these were several of the photos taken around Berkeley campus with the same parameters (position, 30-second exposure time, etc.):</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image1.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Campanile (small)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image2.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Campanile (medium)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image3.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Campanile (large)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image4.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Glade (small)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image5.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Glade (medium)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image6.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Glade (large)</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>Seemingly, as the size of the pinhole increases, the brighter the picture becomes due to exposure. When the hole is too small, there is not enough light for satisfactory detail; when too large, the picture suffers from the same problem but due to too much light instead. Deciding that the <code>1.0mm</code> pinhole looked best, we took 3 self-portraits with it and also a few pics of campus.</p>
            
            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image7.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Mrina</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image8.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Jacob</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image9.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Gibbes</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image10.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">East Asian Library</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image11.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Campbell Hall</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/camera/part1_image12.JPG")} width="300px" alt="camera"/>
                        <figcaption align="middle">Cory</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>The Cory picture is particularly interesting because of the way the ceiling light is dimmed significantly over areas not exposed to the direct light. In the future, of course, we would like to (1) center the camera so that the borders aren't filled with black and (2) use non-red duct tape to avoid that red glare.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/camera/part1_image15.jpg")} width="600px" alt="camera"/>
                        <figcaption align="middle">Pinhole Selfie Process</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

        </div>
        );
    }
}



