import React from 'react';
import './Aside.css';
import Button from './Button.js';

export default class Aside extends React.Component {
  constructor(props) {
    super(props);
    this.setColor = this.setColor.bind(this);
    this.defaultColor = "#b0660e";
    this.clickColor = "#894a00";
    this.state = {colors: [this.clickColor , this.defaultColor, this.defaultColor, this.defaultColor, this.defaultColor]};
  }

  setColor(index) {
    var newColors = [this.defaultColor, this.defaultColor, this.defaultColor, this.defaultColor, this.defaultColor];
    newColors[index] = this.clickColor;
    this.setState({colors: newColors});
  }

  render() {
    return (
      <aside className="Aside">
        <h1 className="Aside-header">Michael Gibbes</h1>
        <div className="Aside-buttons">
            <Button index={0} color={this.state.colors[0]} name="Home"      handleClick={this.props.onPageChange} setColor={this.setColor}/>
            <Button index={1} color={this.state.colors[1]} name="CS 61A"    handleClick={this.props.onPageChange} setColor={this.setColor}/>
            <Button index={2} color={this.state.colors[2]} name="Portfolio" handleClick={this.props.onPageChange} setColor={this.setColor}/>
            <Button index={3} color={this.state.colors[3]} name="Resume"    handleClick={this.props.onPageChange} setColor={this.setColor}/>
            <Button index={4} color={this.state.colors[4]} name="About"     handleClick={this.props.onPageChange} setColor={this.setColor}/>
        </div>
      </aside>
    );
  }
}



