import React from 'react';

export default class Color extends React.Component {
    render() {
        return (
        <div>
            <h1 align="center">Project 1: Images of the Russian Empire</h1>

            <h3 align="center">Part 1: Single-Scale Implementation</h3>

            <p>My first implementation uses the brute force way of colorizing the Prokudin-Gorskii photos. Below are several low-res originals from his collection:</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part1_image1.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Cathedral</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part1_image2.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Settlers</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part1_image3.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Nativity</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>These images are organized in BGR format, where the top photo is blue filtered light, etc. I aligned the three channels automatically by applying a sum of squared differences (SSD) equation:</p>
            <p><code>ssd = sum_i(sum_j((im1[i][j] - im2[i][j])**2))</code></p>
            <p>I test an offset of plus or minus ~20 pixels, using the alignment with the smallest SSD.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part1_image4.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Cathedral</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part1_image5.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Settlers</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part1_image6.jpg")} width="300px" alt="color"/>
                        <figcaption align="middle">Nativity</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>However, it seemed I was missing something important. Look what happened when applying the same algorithm to a picture of a monastery.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part1_image7.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Quite Misaligned...</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>It turns out that the aligning algorithm is correct, but sometimes it fits to darker portions of the photo. In this case, the edges of the photo.</p>
            <p>I clipped off 5% of the pictures to remove the black and white borders, which seemed to do the trick.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part1_image8.jpg")} width="400px" alt="color"/>
                        <figcaption align="middle">Monastery</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part1_image9.jpg")} width="400px" alt="color"/>
                        <figcaption align="middle">Settlers</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>That concludes my single-scale search for aligning the RGB channels of these photos. The next part will be focusing on a method for making this process more efficient.</p>

            <h3 align="center">Part 2: Multi-Scale Implementation</h3>

            <p>The implementation with single-scale is costly when iterating over images with more than 400x400px sizes -- exponentially so. One practical optimization is simply to scale down the image, like a pyramid.</p>
            <p>For example, here are the pyramids corresponding to one image:</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part2_image1.png")} width="400px" alt="color"/>
                        <figcaption align="middle">R</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part2_image2.png")} width="400px" alt="color"/>
                        <figcaption align="middle">G</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part2_image3.png")} width="400px" alt="color"/>
                        <figcaption align="middle">B</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>Once pyramidized, the key to optimal search is traversing the pyramid from top to bottom. I set my search box to be the current level height + 1. This value is arbitrary but works surprisingly well. At the largest image level (0), the search is no more than 1 pixel, or iterating over the image 9 times. Search takes no longer than 10 seconds on average and is accurate in general.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part2_image4.jpg")} width="600px" alt="color"/>
                        <figcaption align="middle">Full</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>Here are a few more examples of this algorithm applied purely.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part2_image5.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Swords</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part2_image6.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Train</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>Per some suggestions that arose from discussing with some other students in this class, I realized that the green channels were actually better as an alignment base. You can see the difference in the photos of the emir.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part2_image7.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Red-Aligned</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part2_image8.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Green-Aligned</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part2_image9.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Blue-Aligned</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <h3 align="center">Part 3: Bells and Whistles</h3>

            <h2>Auto Contrast</h2>

            <p>For auto-contrasting, my process involves shifting the entire image down until the minimum value is 0.0, and then stretching it out so that the maximum value is 1.0. The effect is more noticeable on darker areas, like the main dome of this church.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part3_image1.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">No Contrast</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part3_image2.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">Auto Contrast</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <h2>Auto Balance</h2>

            <p>To enable automatic white balancing, I use the following process</p>
            <ol>
                <li>Approximation of the illuminant... Take the average color to receive an R', G', and B', expressed as floating points between 0 and 1.</li>
                <li>Calculate the proportion that each float dominates the illuminant to get 3 integers. For example, proportion r = R' / (R' + G' + B').</li>
                <li>Multiply every pixel by the inverse of its proportion (r, g, or b) multiplied by 3. A pixel containing exactly the average color should now be precisely balanced as a shade of gray.</li>
            </ol>

            <p>This method works quite well for images taken inside or with artificial lighting such as this sword display in Zlatoust.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part3_image3.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">No Balance</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part3_image4.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">White Balanced</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>It fails when an image already contains a rich, saturated color, like this very blue sky in which the algorithm has a distinct bias for red.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part3_image5.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">No Balance</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part3_image6.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">White Balanced</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <p>And one more example...</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}}>
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/part3_image7.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">No Balance</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/part3_image8.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">White Balanced</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <h3 align="center">Final: Results on All Images</h3>

            <p>Here are the final results on running my algorithm over every image, plus a few of my own I found from the Library of Congress. Underneath each image is its alignment.</p>

            <div align="center">
                <br/>
                <table style={{width: "100%"}} className="final-data">
                    <tbody>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image1.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (1, 7), B: (-2, -5)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image2.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (1, 6), B: (-2, 3)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image3.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-1, 4), B: (-1, -3)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image4.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-1, 8), B: (0, -7)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image5.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (0, 96), B: (-10, -61)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image6.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (17, 57), B: (-24, -49)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image7.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-3, 65), B: (-16, -60)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image8.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-13, 59), B: (-7, -51)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image9.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (5, 48), B: (-17, -40)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image10.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (3, 63), B: (-8, -53)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image11.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (8, 98), B: (-28, -78)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image12.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-5, 69), B: (-17, -67)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image13.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (-1, 58), B: (-11, -54)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image14.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (27, 43), B: (-5, -43)</figcaption>
                    </td>
                    </tr>
                    <tr>
                    <td align="center">
                        <img src={require("./img/color/final_image15.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (7, 61), B: (-19, -56)</figcaption>
                    </td>
                    <td align="center">
                        <img src={require("./img/color/final_image16.jpg")} width="480px" alt="color"/>
                        <figcaption align="middle">R: (10, 72), B: (-11, -64)</figcaption>
                    </td>
                    </tr>
                    </tbody>
                </table>
            </div>

        </div>

        
        );
    }
}



