import React from 'react';
import './ScrollPanel.css';

export class ScrollPanelItem extends React.Component {
    render() {
        return (
        <li onClick={() => this.props.handlePageChange(this.props.title)} className="ScrollPanel-item">
            <figure>
                <img src={this.props.src} alt={this.props.alt}/>
              <figcaption>
                <p>{this.props.title}</p>
              </figcaption>
            </figure>
            <div className="ScrollPanel-date">{this.props.date}</div>
          </li>
        );
    }
}

export default class ScrollPanel extends React.Component {
    render() {
        return (
        <figure className="ScrollPanel">
          <ul className="ScrollPanel-list">
            {this.props.items}
          </ul>
        </figure>
        );
    }
}



