import React from 'react';

export default class ANConcept extends React.Component {
    render() {
        return (
        <div>
			<h1>Alien Nations</h1>
			<h2>By Jaco Beneduci and Michael Gibbes</h2>
		    <p>In this game, the player controls Spurdo, a wandering space walrus intent on making trade with alien societies. 
				Spurdo will have to travel between planets, avoiding hazards along the way. 
				His trusty companion a.k.a. lazy hitchhiker Owen is there to accompany him in his trade conquests.</p>
			<h2>Concept Art</h2>
			<div align="center">
				<br/>
				<table style={{width: "100%"}}>
					<tbody>
					<tr>
					<td>
						<img src={require("./img/an/Spurdo.jpg")} alt="spurdo" width="400px"/>
						<figcaption align="middle">Spurdo the Space Walrus</figcaption>
					</td>
					<td>
						<img src={require("./img/an/Owen.jpg")} alt="owen" width="400px"/>
						<figcaption align="middle">Owen, the Companion</figcaption>
					</td>
					</tr>

					<tr>
					<td>
						<img src={require("./img/an/Spurdo Turnaround (brighter).jpg")} alt="character sheet" width="400px"/>
						<figcaption align="middle">Character Sheet</figcaption>
					</td>
					<td>
						<img src={require("./img/an/Spurdo Walk Cycle 1.jpg")} alt="walk cycle" width="400px"/>
						<figcaption align="middle">Walk Cycle</figcaption>
					</td>
					</tr>

					<tr>
					<td>
						<img src={require("./img/an/Spurdo midrun and jump concept 1.jpg")} alt="jump" width="400px"/>
						<figcaption align="middle">Jumping Spurdo</figcaption>
					</td>
					<td>
						<img src={require("./img/an/Space Truck.jpg")} alt="truck" width="400px"/>
						<figcaption align="middle">Space Semi</figcaption>
					</td>
					</tr>
					
					<tr>
					<td>
						<img src={require("./img/an/Space Port.jpg")} alt="port" width="400px"/>
						<figcaption align="middle">Kooky Jakkir's Space Bazaar</figcaption>
					</td>
					<td>
						<img src={require("./img/an/Thuvix Pirate.jpg")} alt="pirate" width="400px"/>
						<figcaption align="middle">Pirate Design</figcaption>
					</td>
					</tr>   
					</tbody>
				</table>
			</div>
			<h2>The Alien Races</h2>
			<div className="races">
				<div className="race">
					<h2>Asherog</h2>
					<h3>Destroyer</h3>
					<div className="description">
						<p>You sense a powerful presence. Its mind is such chaos, its demeanor stoic. 
							There in horror, you realize it is a demon from the stars. A devourer.</p>
					</div>
					<img src={require("./img/an/Asherog.jpg")} alt="Asherog"/>
				</div>
				<div className="race">
					<h2>Arka</h2>
					<h3>Scholar</h3>
					<div className="description">
						<p>A scholarly race, the Arka would talk nonstop about creation. 
							They specialize in history of solar systems as well as many wars fought on it.</p>
					</div>
					<img src={require("./img/an/Arka.jpg")} alt="Arka"/>
				</div>
				<div className="race">
					<h2>Astren</h2>
					<h3>Creator</h3>
					<div className="description">
						<p>A star angel with plans of genesis borne of boredom. 
							Its children are the alien races of the universe and it has the power to shape the universe.</p>
					</div>
					<img src={require("./img/an/Astren.jpg")} alt="Astren"/>
				</div>
				<div className="race">
					<h2>Benusi</h2>
					<h3>Trucker</h3>
					<div className="description">
						<p>Space walruses, a walrus-like homonid, probably the best space truckers. 
							They possess overbiting tusks and sport enormous whisker beards.</p>
					</div>
					<img src={require("./img/an/Benusi.jpg")} alt="Benusi"/>
				</div>
				<div className="race">
					<h2>Buzari</h2>
					<h3>Merchant</h3>
					<div className="description">
						<p>An extremely wealthy alien race that is known for trading. 
							They are often known to have rusty-red skin and white hair and bioluminescent blue eyes.</p>
					</div>
					<img src={require("./img/an/Buzari.jpg")} alt="Buzari"/>
				</div>
				<div className="race">
					<h2>Charn</h2>
					<h3>Telepath</h3>
					<div className="description">
						<p>The charn can read minds through electro-receptors in their snouts. 
							Using telepathic suggestion, they can subconsciously influence the actions of others.</p>
					</div>
					<img src={require("./img/an/Charn.jpg")} alt="Charn"/>
				</div>
				<div className="race">
					<h2>Leonid</h2>
					<h3>Warden</h3>
					<div className="description">
						<p>The Leonids are a feline race with an authoritative presence and behavior. 
							Their home-planet is lush, green and full of beauty.</p>
					</div>
					<img src={require("./img/an/Leonid.jpg")} alt="Leonid"/>
				</div>
				<div className="race">
					<h2>Rachnian</h2>
					<h3>Pirate</h3>
					<div className="description">
						<p>Rachnians are fiercely independent and born leaders. 
							There has been no concrete method of government save the Rachnian code.</p>
					</div>
					<img src={require("./img/an/Rachnian.jpg")} alt="Rachnian"/>
				</div>
				<div className="race">
					<h2>Suronian</h2>
					<h3>Warrior</h3>
					<div className="description">
						<p>Suronians are hominid dinosaurs sired by the Astren to function as excellent shock infantry. 
							They are an industrial warrior race.</p>
					</div>
					<img src={require("./img/an/Suronian.jpg")} alt="Suronian"/>
				</div>
				<div className="race">
					<h2>Thuvix</h2>
					<h3>Mechanic</h3>
					<div className="description">
						<p>An alien race that lives in asteroid belts, scavenges space trash and make a particular profit off of mining. 
							They are notorious mechanics.</p>
					</div>
					<img src={require("./img/an/Thuvix.jpg")} alt="Thuvix"/>
				</div>
			</div>
        </div>
        );
    }
}



