import React from 'react';
const ReactCSSTransitionGroup = require('react-addons-css-transition-group');
const Draggable = require('react-draggable');

class Fact extends React.Component {
        constructor(props) {
                super(props);
                this.state = {height : 400};
        }

        componentDidMount() {
                const height = this.divElement.clientHeight;
                this.setState({ height });
        }

        render() {
                var topHeight = 0;
                if (this.state.height > 400) topHeight = 400 - this.state.height;
                return (
                <Draggable axis="y" bounds={{bottom: 0, top: topHeight}}>
                        <div ref={ (divElement) => this.divElement = divElement}>
                                {this.props.content}
                        </div>
                </Draggable>
                );
        }
}

export default class PythonFacts extends React.Component {
        constructor(props) {
                super(props);
                this.pythonFacts = [

                /*** BINOPS ***/
                <Fact key="binary" content={
                <div>
                        <h2>Numbers</h2>
                        <p>Regarding binary (two-argument) operations involving <code>float</code> and <code>int</code>...</p>
                        <p>Given <code>int</code> and <code>int</code>, the result is an <code>int</code>: </p>
                        <pre>{
`>>> type(9 + 4)
<class 'int'>
>>> type(-9 - 4)
<class 'int'>`
                }
                        </pre>
                        <p>Otherwise, the result is a <code>float</code>:</p>
                        <pre>{
`>>> type(9.0 + 4)
<class 'float'>
>>> type(-9 // 4.0)
<class 'float'>`
                }
                        </pre>
                        <p>One exception to this rule is <strong>real division</strong>, represented by <code>/</code>, which always returns a <code>float</code>.</p>
                </div>}/>,

                /*** SCOPING ***/
                <Fact key="global" content={
                <div>
                        <h2>Special Access</h2>
                        <p>The <code>global</code> and <code>nonlocal</code> keywords modify the default variable behavior.</p>
                        <p>Normally, variable names are unique to their function scope.</p>
                        <pre>{
`>>> def f(x):
...     def g():
...             x = 2
...     g()
...     print(x)
...
>>> f(3)
3`
                }
                        </pre>
                        <p><code>nonlocal</code> gives access to an outer, nonglobal variable.</p>
                        <pre>{
`>>> def f(x):
...     def g():
...             nonlocal x
...             x = 2
...     g()
...     print(x)
...
>>> f(3)
2`
                }
                        </pre>
                        <p><code>global</code> gives access to global variables.</p>
                        <pre>{
`>>> def f(x):
...     def g():
...             global x
...             x = 2
...     g()
...     print(x)
...
>>> x = 0
>>> f(3)
3
>>> x
2`
                }
                        </pre>
                        <p>Take caution when using these keywords, especially <code>global</code>, since they make it harder to keep track of variable values.</p>
                </div>}/>,

                /*** ELSE ***/
                <Fact key="else" content={
                <div>
                        <h2>Or Else</h2>
                        <p>Did you know that <code>if</code> is not the only statement that can be followed by a <code>else</code>?</p>
                        <p>In a <code>while</code> loop, the loop enters the <code>else</code> statement when the condition is false.</p>
                        <pre>{
`>>> x = 0
>>> while x < 2:
...     print('Boba ', end='')
...     x += 1
... else:
...     print('Fett')
...
Boba Boba Fett`
                }
                        </pre>
                        <p><code>else</code> can also be used with <code>for</code> and <code>try</code>.</p>
                        <p>Using a <code>break</code> statement prevents <code>else</code> from executing.</p>
                </div>}/>,

                /*** GENERATORS ***/
                <Fact key="yield" content={
                <div>
                        <h2>How About Yield</h2>
                        <p>Generators are a special type of Python iterator.</p>
                        <p>When Python parses a function's body and encounters a <code>yield</code> statement, that function now always returns a generator.</p>
                        <pre>{
`>>> def pok(emon):
...     games =['red','gold','sapphire']
...     for i in range(len(games)):
...             yield games[i]
...`
                }
                        </pre>
                        <p>Calling the function, <code>pok</code> no longer causes the body of the function to be executed.</p>
                        <pre>{
`>>> generations = pok(42)`
                }
                        </pre>
                        <p>As mentioned earlier, generators are iterators, so activating the generator body requires iteration.</p>
                        <pre>{
`>>> for game in generations: print(game)
...
red
gold
sapphire`
                }
                        </pre>
                        <p>One alternate implementation is to use the equivalent <code>yield from</code> syntax.</p>
                        <pre>{
`>>> def pok(emon):
...     yield from ['red','gold','sapphire']
...`
                }
                        </pre>
                        <p>Note that using <code>return</code> in a generator is often treated as <code>StopIteration</code>, but the behavior of <code>return</code> in the presence of <code>yield</code> depends on the Python version. <strong>I recommend not to mix these keywords</strong>.</p>
                </div>}/>,

                /*** PYTHON CORE ***/
                <Fact key="foundation" content={
                <div>
                        <h2>Object to Everything</h2>
                        <p>The bread and butter of Python data is objects. According to the docs...</p>
                        <p>"Every object has an identity, a type and a value. An objects identity and type never change once it has been created."</p>
                        <pre>{
`>>> abc = ['a', 'b', 'c']
>>> id(abc)
140555353769416
>>> type(abc)
<class 'list'>`
                }
                        </pre>
                        <p>For example, the variable <code>abc</code> references an object of <code>type</code> <code>list</code> with a unique integer <code>id</code> <code>140555353769416</code> and value <code>['a', 'b', 'c']</code>.</p>
                </div>}/>,

                /*** CONTAINER IDS ***/
                <Fact key="containers" content={
                <div>
                        <h2>Mutation</h2>
                        <p>Objects containing references to other objects are said to be <strong>containers</strong>, like <code>tuple</code> or <code>list</code>.</p>
                        <pre>{
`>>> crash = (1, 2, [3, 4])`
                }
                        </pre>           
                        <p>Mutability refers to whether you can change the quantity or quality of the <code>id</code>s stored in the container.</p>
                        <p><code>tuple</code>, for example, is immutable.</p>
                        <pre>{
`>>> crash[2] = 3 
TypeError: 'tuple' object`
                }
                        </pre> 
                        <p><code>list</code> is mutable and the <code>id</code> of its contents can change.</p>
                        <pre>{
`>>> id(crash[2][1])
10832704
>>> crash[2][1] = 5
>>> id(crash[2][1])
10832736`
                }
                        </pre>
                        <p>Notice how the <strong>value</strong> of an immutable container can still change.</p>
                </div>}/>,

                /*** GENERAL IDS ***/
                <Fact key="identity" content={
                <div>
                        <h2>Identity Crisis</h2>
                        <p>Remember not to fall into the trap of associating <code>id</code> with value, especially for integers.</p>
                        <p><code>id</code> can be the same for integers of the same value.</p>
                        <pre>{
`>>> id(5)
10832736
>>> 5 is 5 and 5 == 5
True`
                }
                        </pre>
                        <p>Or <code>id</code> can be slightly different.</p>
                        <pre>{
`>>>2**50 == 2**50
True
>>> 2**50 is 2**50
False
>>> id(2**50)
1175213111568
>>> id(2**50)
1175213111600`
                }
                        </pre>
                        <p>This is true of large integers especially because of number representation.</p>
                </div>}/>,

                /*** DIR ***/
                <Fact key="docs" content={
                <div>
                        <h2>Documentation Hack</h2>
                        <p>Ever think there are too many weird rules and utility functions you don't know?</p>
                        <p>The <code>dir</code> function is your friend!</p>
                        <pre>{
`>>> dir(int)
['__abs__', '__add__', '__and__', ...`
                }
                        </pre>
                        <p><code>dir</code> tells you all the operators for a specified data type.</p>
                        <p>To learn more about each, you can review the <a href="https://rszalski.github.io/magicmethods/">magic method</a> manual or use trusty <a href="http://lmgtfy.com/?q=python+magic+methods">Google</a>.</p>
                </div>}/>,

                /*** ARG UNPACKING ***/
                <Fact key="args" content={
                <div>
                        <h2>Unpacking the Goods</h2>
                        <p>You have a sequence of two elements and a function that takes two arguments.</p>
                        <pre>{
`>>> oski = 'oski', '4life'
>>> def concat(x, y):
...     return x + y
...`
                }
                        </pre>
                        <p>You could input them individually.</p>
                        <pre>{
`>>> concat(oski[0], oski[1])
'oski4life'`
                }
                        </pre>
                        <p>Or you could use Python's sequence-unpacking feature.</p>
                        <pre>{
`>>> concat(*oski)
'oski4life'`
                }
                        </pre>
                        <p>This method works for any iterable sequence such as <code>list</code> or <code>tuple</code>.</p>
                        <p>In addition, <code>dict</code> unpacking can merge or input function parameters.</p>
                        <pre>{
`>>> go = {'x': 'oski'}
>>> bears = {'y': '4life'}
>>> oski = {**go, **bears}
>>> oski
{'x': 'oski', 'y': '4life'}
>>> concat(**oski)
'oski4life'`
                }
                        </pre>
                        <p>Have fun!</p>
                </div>}/>,

                /*** TYPE HINTS ***/
                <Fact key="typing" content={
                <div>
                        <h2>Python Type Hints</h2>
                        <p>One feature that makes <code>Python</code> unique is <strong>dynamic typing</strong>. In other words, a variable can hold any type of value.</p>
                        <pre>{
`>>> def combine(a, b):
...     return a + b
...
>>> combine(1, 2)
3
>>> int(combine("1", "2"))
12`
                }
                        </pre>
                        <p>In other languages, like the <code>C</code> family and <code>Java</code>, <strong>static typing</strong> is mandatory.</p>
                        <pre>{
`string combine(string a, string b) 
{
        return a + b;
}`
                }
                        </pre>
                        <p>A little-known fact, Python offers its own version of "static types" called <strong>type hints</strong>.</p>
                        <p>Let's say <code>combine</code> is now a function that takes in two <code>str</code> and returns one <code>str</code>.</p>
                        <pre>{
`>>> def combine(a: str, b: str) -> str:
...     return a + b
...
>>> combine('Heroes ', 'never die')
'Heroes never die'`
                }
                        </pre>
                        <p>It is important to note that type hints do not improve performance and mean nothing to the interpreter.</p>
                        <p>Watch what happens when we give <code>combine</code> two <code>int</code> args.</p>
                        <pre>{
`>>> combine(1, 2) # Python don't care, lol
3`
                }
                        </pre>
                        <p>Arguably this syntax brings readability to the code and helps certain IDEs like PyCharm perform type checking. It also does not conflict with previous versions of Python.</p>
                        <p>However, Python is and will remain a dynamically-typed language.</p>
                </div>}/>,

                /*** SHORTENING SYNTAX ***/
                <Fact key="docs" content={
                <div>
                        <h2>Syntax Hack</h2>
                        <p>Consider this genius script.</p>
                        <pre>{
`>>> x = 5
>>> if 1 < x < 10:
...     print("o hi mark")
... else:
...     print("o hi jonny")
...
o hi mark`
                }
                        </pre>
                        <p>Suites can be written on the same line if they are a single expression.</p>
                        <pre>{
`>>> x = 5
>>> if 1 < x < 10: print("o hi mark")
... else: print("o hi jonny")
...
o hi mark`
                }
                        </pre>
                        <p>A <strong>ternary expression</strong> <code>Q1 if P else Q2</code> is equivalent to the above code.</p>
                        <pre>{
`>>> x = 5
>>> print("o hi mark") if 1 < x < 10 \\
... else print("o hi jonny")
o hi mark`
                }
                        </pre>
                        <p>Or maybe instead of using a new line to separate statements, use <code>;</code>.</p>
                        <p><code>&gt;&gt;&gt; x = 5 ; print("o hi mark") if 1 &lt; x &lt; 10 else print("o hi jonny")</code></p>
                        <p>Cramming your script together like this might not amuse your coding partners, though...</p>
                </div>}/>,

                /*** CALLABLE ***/
                <Fact key="args" content={
                <div>
                        <h2>Objectionable Secrets</h2>
                        <p>Callables are objects that can activate frames. Typically you see them in the form of <code>func(*args)</code>.</p>
                        <p>Pssst. These objects actually contain a few secrets.</p>
                        <pre>{
`>>> def f(): return lambda: 'junipero'
...
>>> f.__name__
'f'`
                }
                        </pre>
                        <p>Callables have an explicit name. Ever wish you could name a lambda?</p>
                        <pre>{
`>>> g = f()
>>> g
<function f.<locals>.<lambda> at ...>
>>> g.__qualname__ = 'san ' + g()
>>> g
<function san junipero at ...>`
                }
                        </pre>
                        <p>Callables even store outer variables to which they have access.</p>
                        <pre>{
`>>> f.__globals__
{'__name__': '__main__', ...}`
                }
                        </pre>
                        <p>For info about making your own callable objects, check <a href="https://en.wikipedia.org/wiki/Callable_object">the wiki</a>.</p>
                </div>}/>,

                /*** TRUTH ***/
                <Fact key="truth" content={
                <div>
                        <h2>Truthiness</h2>
                        <p>Python has a <strong>truthy</strong> unless proven <strong>falsey</strong> policy for its objects.</p>
                        <p>If the <code>__bool__</code> method is defined, then Python will use that to judge the object.</p>
                        <pre>{
`>>> class Hobbit:
...     ring = False
...     def steal(self):
...             self.ring = True
...     def __bool__(self):
...             return not self.ring
...
>>> frodo = Hobbit()
>>> if frodo: print("nice hobbitses")
...
nice hobbitses
>>> frodo.steal()
>>> if frodo: print("nice hobbitses")
...`
                }
                        </pre>
                        <p>If the <code>__bool__</code> method is not defined, Python attempts to call the <code>__len__</code> method. Objects with length <code>0</code> are falsey.</p>
                </div>}/>,

                /*** MAGIC METHODS ***/
                <Fact key="magic" content={
                <div>
                        <h2>Magic Methods</h2>
                        <p>Ever want to add two squirrels together? Now you can.</p>
                        <pre>{
`>>> class Squirrel:
...     def __init__(self, word): 
...             self.word = word
...     def __add__(self, ot): 
...             return self.word + ot.word
...
>>> bob = Squirrel('believe in ')
>>> sherry = Squirrel('magic')
>>> bob + sherry
'believe in magic'`
                }
                        </pre>
                        <p>Magic methods, naming convention <code>__method__</code>, are called in unusual circumstances (i.e. not normal <code>func(*args)</code>).</p>
                        <p>The <code>__add__</code> method, for example, is called when two expressions are separated by a <code>+</code>.</p>
                        <p>A full list of magic methods is available at the <a href="https://rszalski.github.io/magicmethods/">magic method</a> manual or trusty <a href="http://lmgtfy.com/?q=python+magic+methods">Google</a>.</p>
                </div>}/>,

                /*** WITH STATEMENT ***/
                <Fact key="with" content={
                <div>
                        <h2>Clean Up Your Mess</h2>
                        <p>The <code>with</code> statement offers code reusability that its alternative (<code>try</code>) does not.</p>
                        <p>We need a class with the magic methods <code>__enter__</code> and <code>__exit__</code> to use <code>with</code>.</p>
                        <pre>{
`>>> class AI:
...     def __enter__(self):
...             print("CAKE")
...             return "PROMISES"
...     def __exit__(self, type, val, trace):
...             if type:
...                     print("LIES")
...                     print("e: ", type)
...             print("EXIT")
...             return True
...`
                }
                        </pre>
                        <p>Example usage:</p>
                        <pre>{
`>>> with AI() as glados:
...     print(glados)
...
CAKE
PROMISES
EXIT`
                }
                        </pre>
                        <p>Every <code>with</code> statement is processed as follows:</p>
                        <ol>
                                <li>Call <code>__enter__</code> method of <code>AI</code>. (print <code>CAKE</code>)</li>
                                <li>Assign return value to <code>glados</code>. (return <code>PROMISES</code>)</li>
                                <li>Execute body of <code>with</code>. (print <code>PROMISES</code>)</li>
                                <li>Call <code>__exit__</code> method of <code>AI</code>. (print <code>EXIT</code>)</li>
                        </ol>
                        <p>No matter what <code>__exit__</code> will always execute. If <code>__exit__</code> returns <code>True</code> then any errors will be silenced.</p>
                        <pre>{
`>>> with AI() as chell:
...     thecakeisalie
...
CAKE
LIES
e:  <class 'NameError'>
EXIT`
                }
                        </pre>
                        <p>The file-reading feature <code>open('filename.txt')</code>, which already defines magic methods <code>__enter__</code> and <code>__exit__</code>, is commonly used with <code>with</code>.</p>
                </div>}/>,
                
                /*** EMPTY TUPLE ***/
                <Fact key="emptytuple" content={
                <div>
                        <h2>Not the Tuple We Deserved</h2>
                        <p>When attempting to assign multiple variables at once, we refer to this structure as the <strong>target list</strong>.</p>
                        <pre>{
`>>> [took, an, arrow] = 'to', 'the', 'knee'
>>> took
'to'
>>> arrow
'knee'`
                }
                        </pre>
                        <p>Target list assignment sparked a discussion in early 2015 when this happened.</p>
                        <pre>{
`>>> [] = ()
>>> () = []
  File "<stdin>", line 1
SyntaxError: can't assign to ()`
                }
                        </pre>
                        <p>Shouldn't target list assignment of an empty <code>list</code> to an empty <code>tuple</code> work?</p>
                        <p>On one hand, the source of the interpreter's logic is understandable. The empty <code>tuple</code> is always the same object.</p>
                        <pre>{
`>>> [] is []
False
>>> () is ()
True`
                }
                        </pre>
                        <p>However, Python developers made the nitpicky distinction in the end to allow the statement <code>() = []</code>, and today, this statement is completely legal.</p>
                </div>}/>,

                /*** FUNCTIONAL PROGRAMMING ***/
                <Fact key="functional" content={
                <div>
                        <h2>Barely Functional</h2>
                        <p>Python is... well... <a href="https://www.quora.com/Is-Python-a-functional-language">barely functional</a>.</p>
                        <p>Its creator, Guido van Rossum, ardently follows a "Pythonic creed" that rejects the philosophies of many other languages.</p>
                        <pre>{
`>>> import this
The Zen of Python, by Tim Peters

Beautiful is better than ugly...`
                }
                        </pre>
                        <p><a href="http://neopythonic.blogspot.com/2009/04/tail-recursion-elimination.html">Guido would probably fundamentally disagree</a> with most content of 61A, UC Berkeley's introduction to programming:</p>
                        <p><strong>"I don't believe in recursion as the basis of all programming."</strong></p>
                        <p><strong>"Python-style lists... are much more useful to start exploring the wonderful world of programming than recursion."</strong></p>
                        <p><strong>"Using a linked list to represent a sequence of value [sic] is distinctly unpythonic, and in most cases very inefficient."</strong></p>
                        <p>Guido didn't even want to include our beloved <code>lambda</code> in Python 3.</p>
                        <pre>{
`>>> half = lambda: 2`
                }
                        </pre>
                        <p>Sadly, Pythonic <code>lambda</code> is indeed just a cheesed-up version of a normal <code>def</code> function.</p>
                        <pre>{
`>>> def half():
...     return 2
...
>>> half.__qualname__ = '<lambda>'`
                }
                        </pre>
                        <p>The above code is equivalent in every way to the normal <code>lambda</code>.</p>
                </div>}/>,

                ];
                var index = Math.floor(Math.random() * this.pythonFacts.length);
                this.state = {index};
                this.handleForward = this.handleForward.bind(this);
                this.handleBackward = this.handleBackward.bind(this);
        }

        handleForward() {
                var index = this.state.index + 1;
                if (index >= this.pythonFacts.length) index = 0;
                this.setState({index});
        }

        handleBackward() {
                var index = this.state.index - 1;
                if (index < 0) index = this.pythonFacts.length - 1;
                this.setState({index});
        }

        render() {
                return (
                <div className="PythonFacts">
                        <div className="PythonFacts-left" onClick={this.handleBackward}>&lt;</div>
                        <figure className="PythonFacts-panel">
                                <ReactCSSTransitionGroup
                                        transitionName="example"
                                        transitionEnterTimeout={500}
                                        transitionLeave={false}>
                                                {this.pythonFacts[this.state.index]}
                                </ReactCSSTransitionGroup>
                        </figure>
                        <div className="PythonFacts-right" onClick={this.handleForward}>&gt;</div>
                </div>
                );
        }
}



