import React from 'react';

export default class ANProto extends React.Component {
    render() {
        return (
        <div>
            <h3>Final Project Prototype</h3>
            <p><a href="https://drive.google.com/file/d/1ZMfcai5JMaGXwIt4Oq8UhigBA_QR59g9/view">Click here</a> to download a playable executable of this game, developed by me and Jaco Beneduci.</p>
            <p>"Alien Nations" is a 2D space game that revolves around orbit exploration and planetary interaction.</p>
            <p>At the moment, progress has been discontinued. If time allows, we plan to return to this concept to develop it further.</p>
        </div>
        );
    }
}



